import { keyShortcuts, codeObj } from './keyShortcuts';

export default function changeToolKey(event) {
    const tools = keyShortcuts;
    const keyMessage = document.getElementById('key-message');
    const modalError = document.getElementById('modal-error');
    const toolsContainerModal = document.querySelector(
        '.tools-container-modal',
    );
    let toolClickedElement = null;

    addKeyListener(event.target);

    function addKeyListener(element) {
        if (
            element.classList.contains('tools-container-modal') ||
            element.classList.contains('tools-modal-column')
        ) {
            return;
        }

        if (element.classList.contains('tool-modal')) {
            keyMessage.classList.add('key-massage-show');
            toolsContainerModal.style.pointerEvents = 'none';

            toolClickedElement = element;
            toolClickedElement.classList.add('button-choose');
            document.addEventListener('keydown', findTool);
            return;
        }

        addKeyListener(element.parentElement);
    }

    function findTool(e) {
        const key = e.which;

        if (toolClickedElement.classList.contains('modal-pen')) {
            changeKey('pen', key);
        }

        if (toolClickedElement.classList.contains('modal-eraser')) {
            changeKey('eraser', key);
        }

        if (toolClickedElement.classList.contains('modal-bucket')) {
            changeKey('bucket', key);
        }

        if (toolClickedElement.classList.contains('modal-paint-all')) {
            changeKey('paintAll', key);
        }

        if (toolClickedElement.classList.contains('modal-stroke-line')) {
            changeKey('line', key);
        }

        if (toolClickedElement.classList.contains('modal-rectangle')) {
            changeKey('rectangle', key);
        }

        if (toolClickedElement.classList.contains('modal-circle')) {
            changeKey('circle', key);
        }

        if (toolClickedElement.classList.contains('modal-dithering')) {
            changeKey('dithering', key);
        }

        if (toolClickedElement.classList.contains('modal-lighten')) {
            changeKey('lighten', key);
        }

        if (toolClickedElement.classList.contains('modal-rotate')) {
            changeKey('rotate', key);
        }

        if (toolClickedElement.classList.contains('modal-move')) {
            changeKey('move', key);
        }

        if (toolClickedElement.classList.contains('modal-mirror-pen')) {
            changeKey('mirror', key);
        }

        if (toolClickedElement.classList.contains('modal-color-picker')) {
            changeKey('colorPicker', key);
        }

        if (toolClickedElement.classList.contains('modal-flip')) {
            changeKey('flip', key);
        }

        if (toolClickedElement.classList.contains('modal-magic')) {
            changeKey('magic', key);
        }
    }

    function changeKey(tool, key) {
        const keySpan = toolClickedElement.firstElementChild;

        if (codeObj[key]) {
            keySpan.classList.add('tool-button-error');
            modalError.classList.add('modal-error-show');
        } else {
            const oldKey = tools[tool].code;
            const newKey = String.fromCharCode(key);
            delete codeObj[oldKey];

            codeObj[key] = true;
            tools[tool].code = key;
            keySpan.textContent = newKey;

            if (keySpan.classList.contains('tool-button-error')) {
                keySpan.classList.remove('tool-button-error');
                modalError.classList.remove('modal-error-show');
            }

            toolsContainerModal.style.pointerEvents = 'auto';
            toolClickedElement.classList.remove('button-choose');
            document.removeEventListener('keydown', findTool);
        }
    }
}
