export default class Preview {
    constructor() {
        this._fullScreenButton = document.querySelector('.fullscreen');
        this._previewCanvas = document.querySelector('.preview-canvas');
        this._previewCtx = this._previewCanvas.getContext('2d');
        this._fpsInput = document.getElementById('preview-fps');
        this._fpsText = document.getElementById('display-fps');
        this._fps = this._fpsInput.value;

        this._addPreviewListeners();
        this._startAnimation(Math.floor(1000 / this._fps));
    }

    static getFPS() {
        return document.getElementById('preview-fps').value;
    }

    _startAnimation(interval) {
        let frameNumber = 0;
        this._timerId = setInterval(() => {
            const container = document.getElementById('frames-container');
            const childrens = container.children;
            let currentFrame = null;

            try {
                currentFrame = childrens[frameNumber].children[0].children[0];
            } catch (e) {
                frameNumber = 0;
                currentFrame = childrens[frameNumber].children[0].children[0];
            }

            this._previewCtx.clearRect(
                0,
                0,
                this._previewCanvas.width,
                this._previewCanvas.height,
            );

            this._previewCtx.drawImage(
                currentFrame,
                0,
                0,
                this._previewCanvas.width,
                this._previewCanvas.height,
            );

            frameNumber++;
        }, interval);
    }

    _addPreviewListeners() {
        this._fpsInput.addEventListener('input', () => {
            this._fps = this._fpsInput.value;
            this._fpsText.textContent = `${this._fps} FPS`;

            const interval = Math.floor(1000 / this._fps);
            this._restartAnimation(interval);
        });

        this._fullScreenButton.addEventListener('click', () => {
            this._previewCtx.imageSmoothingEnabled = false;
            this._previewCanvas.requestFullscreen();
        });
    }

    _restartAnimation(interval) {
        clearInterval(this._timerId);
        this._startAnimation(interval);
    }
}
