import { triggerFpsChange, triggerSizeChange } from '../fileManagementUtils';

export default class Import {
    constructor() {
        this._piskelImport = document.getElementById('piskel-import');
        this._piskelForm = document.getElementById('piskel-import-form');

        this._mainCanvas = document.getElementById('canvas');
        this._mainCtx = this._mainCanvas.getContext('2d');
        this._addFrameElement = document.getElementById('add-frame');

        this._addImportButtonsListeners();
    }

    _addImportButtonsListeners() {
        this._piskelForm.addEventListener('input', this._importFile.bind(this));
    }

    _importFile(event) {
        event.preventDefault();

        if (!this._piskelImport.files[0]) {
            return;
        }

        const file = this._piskelImport.files[0];
        const fileReader = new FileReader(file);

        fileReader.readAsText(this._piskelImport.files[0]);

        fileReader.onload = this._changeFrames.bind(this);

        this._piskelForm.reset();
    }

    _changeFrames(e) {
        const result = JSON.parse(e.target.result);
        const fps = +result.piskel.fps;
        const size = +result.piskel.width;
        const frames = JSON.parse(result.piskel.layers[0]);
        const framesCount = frames.frameCount;
        const chunks = frames.chunks;
        const srcPNG = chunks[0].base64PNG;

        triggerFpsChange(fps);
        triggerSizeChange(size);
        const newFrames = this._createNewFrames(framesCount - 1);

        this._fillFrames(newFrames, srcPNG, size);
    }

    _createNewFrames(count) {
        const myFrames = Array.from(document.querySelectorAll('.frame-canvas'));
        const firstFrame = myFrames[0];
        const firstFrameCtx = firstFrame.getContext('2d');
        firstFrameCtx.clearRect(0, 0, myFrames[0].width, myFrames[0].height);

        for (let i = 1; i < myFrames.length; i++) {
            myFrames[i].parentElement.parentElement.remove();
        }

        for (let i = 0; i < count; i++) {
            this._addFrameElement.click();
        }

        return Array.from(document.querySelectorAll('.frame-canvas'));
    }

    _fillFrames(newFrames, srcPNG, size) {
        const tempCanvas = document.createElement('canvas');
        const tempCtx = tempCanvas.getContext('2d');
        const mainSize = this._mainCanvas.width;

        tempCanvas.width = size * newFrames.length;
        tempCanvas.height = size;

        const img = new Image();
        img.src = srcPNG;

        img.onload = () => {
            const lastFrame = newFrames[newFrames.length - 1];
            tempCtx.drawImage(img, 0, 0, tempCanvas.width, tempCanvas.height);

            newFrames.forEach((currentFrame, index) => {
                const ctx = currentFrame.getContext('2d');
                const startPoint = size * index;

                ctx.imageSmoothingEnabled = false;
                ctx.drawImage(
                    tempCanvas,
                    startPoint,
                    0,
                    size,
                    size,
                    0,
                    0,
                    mainSize,
                    mainSize,
                );
            });

            this._mainCtx.drawImage(lastFrame, 0, 0, mainSize, mainSize);
        };
    }
}
