import { getPixelColor, getCoordinates, matchStartColor } from '../ToolsUtils';

export default function magicLogic(event) {
    const delta = this.state.deltaSize;
    const ctx = this._ctx;
    const size = this.state.canvasSize;
    const coordinates = getCoordinates(event, delta);
    const clickX = coordinates[0] * delta;
    const clickY = coordinates[1] * delta;
    const cursorColor = getPixelColor(clickX, clickY, ctx);
    const fullSize = size * delta;
    // get full array of RGBA colors for better performance
    const canvasDataArray = ctx.getImageData(0, 0, fullSize, fullSize).data;
    const isShiftPressed = event.shiftKey;

    if (isShiftPressed) {
        for (let x = 0; x < size; x++) {
            for (let y = 0; y < size; y++) {
                const r = Math.floor(Math.random() * 256);
                const g = Math.floor(Math.random() * 256);
                const b = Math.floor(Math.random() * 256);

                ctx.fillStyle = `rgb(${r},${g},${b})`;
                ctx.fillRect(x * delta, y * delta, delta, delta);
            }
        }
    } else {
        for (let x = 0; x < size; x++) {
            for (let y = 0; y < size; y++) {
                const r = Math.floor(Math.random() * 256);
                const g = Math.floor(Math.random() * 256);
                const b = Math.floor(Math.random() * 256);

                ctx.fillStyle = `rgb(${r},${g},${b})`;

                const matching = matchStartColor(
                    x,
                    y,
                    fullSize,
                    delta,
                    canvasDataArray,
                    cursorColor,
                );

                if (matching) {
                    ctx.fillRect(x * delta, y * delta, delta, delta);
                }
            }
        }
    }
}
