export function download(dataUrl, filename) {
    const a = document.createElement('a');
    a.href = dataUrl;
    a.download = filename;
    a.click();
}

export function createLayoutArray(length) {
    const arr = [];

    for (let i = 0; i < length; i++) {
        const newArr = [];
        newArr.push(i);
        arr.push(newArr);
    }

    return arr;
}

export function createPNG(frames, size) {
    const tempCanvas = document.createElement('canvas');
    const tempCtx = tempCanvas.getContext('2d');
    const width = frames.length * size;
    tempCanvas.width = width;
    tempCanvas.height = size;

    frames.forEach((currentFrame, index) => {
        const frameSize = currentFrame.width;
        const startPoint = 0 + size * index;

        tempCtx.imageSmoothingEnabled = false;

        tempCtx.drawImage(
            currentFrame,
            0,
            0,
            frameSize,
            frameSize,
            startPoint,
            0,
            size,
            size,
        );
    });

    return tempCanvas.toDataURL();
}

export function triggerFpsChange(fps) {
    const input = document.getElementById('preview-fps');

    input.value = fps;
    const event = new CustomEvent('input');
    input.dispatchEvent(event);
}

export function triggerSizeChange(size) {
    const size32 = document.querySelector('.size-32');
    const size64 = document.querySelector('.size-64');
    const size128 = document.querySelector('.size-128');

    if (size === 32) {
        size32.click();
    } else if (size === 64) {
        size64.click();
    } else if (size === 128) {
        size128.click();
    }

    return null;
}
