import Preview from '../../screens/Preview/Preview';
import dragAndDropFrames from './dragAndDropFrames';

export default class Frames {
    constructor() {
        this._canvas = document.getElementById('canvas');
        this._defaultFrame = document.querySelector('.frame');
        this._addFrameElement = document.getElementById('add-frame');
        this._framesContainer = document.getElementById('frames-container');

        this._currentFrameNumber = 0;
        this._previousFrame = null;
        this._currentFrame = this._defaultFrame;

        this._dragAndDropFrames = dragAndDropFrames.bind(this);
        this._preview = new Preview();
        this._addFramesEventListeners();
    }

    static getCurrentFrameCanvas() {
        return document.querySelector('.frame-active').children[0].children[0];
    }

    _addFramesEventListeners() {
        const deleteFrame = document.querySelector('.delete-frame');
        const copyFrame = document.querySelector('.copy-frame');

        document.addEventListener('mouseup', () => {
            const currentFrameCanvas = Frames.getCurrentFrameCanvas();
            const currentFrameCtx = currentFrameCanvas.getContext('2d');
            const frameWidth = currentFrameCanvas.width;
            const frameHeight = currentFrameCanvas.height;
            currentFrameCtx.imageSmoothingEnabled = false;

            currentFrameCtx.clearRect(0, 0, frameWidth, frameHeight);

            currentFrameCtx.drawImage(
                this._canvas,
                0,
                0,
                frameWidth,
                frameHeight,
            );
        });

        this._addFrameElement.addEventListener('click', () => {
            const newFrame = this._createNewFrame();

            this._framesContainer.insertBefore(newFrame, this._addFrameElement);
            this._changeCurrentFrame(newFrame);
            this._updateFrameNumbers();
        });

        this._framesContainer.addEventListener('click', e => {
            if (e.target.classList.contains('frame-canvas')) {
                this._changeCurrentFrame(e.target.parentNode.parentNode);
            }
        });

        this._dragAndDropFrames();
        deleteFrame.addEventListener('click', this._deleteFrame.bind(this));
        copyFrame.addEventListener('click', this._copyFrame.bind(this));
    }

    _changeCurrentFrame(newCurrent) {
        const mainCanvas = document.getElementById('canvas');
        const mainCtx = mainCanvas.getContext('2d');
        const currentFrameCanvas = newCurrent.children[0].children[0];
        mainCtx.imageSmoothingEnabled = false;

        this._previousFrame = this._currentFrame;
        this._previousFrame.classList.remove('frame-active');
        this._currentFrame = newCurrent;
        this._currentFrame.classList.add('frame-active');

        mainCtx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);

        mainCtx.drawImage(
            currentFrameCanvas,
            0,
            0,
            mainCanvas.width,
            mainCanvas.height,
        );
    }

    _createNewFrame() {
        const newFrame = this._defaultFrame.cloneNode(true);
        const deleteFrame = newFrame.children[1];
        const copyFrame = newFrame.children[2];
        deleteFrame.addEventListener('click', this._deleteFrame.bind(this));
        copyFrame.addEventListener('click', this._copyFrame.bind(this));

        return newFrame;
    }

    _deleteFrame(e) {
        if (this._framesContainer.children.length < 3) {
            return;
        }
        e.target.parentNode.remove();
        this._updateFrameNumbers();
        this._changeCurrentFrame(this._addFrameElement.previousElementSibling);
    }

    _copyFrame(e) {
        const newFrame = this._createNewFrame();
        const newFrameCanvas = newFrame.children[0].children[0];
        const newFrameCanvasCtx = newFrameCanvas.getContext('2d');

        const copiedFrame = e.target.parentNode;
        const copiedFrameCanvas = copiedFrame.children[0].children[0];

        this._framesContainer.insertBefore(
            newFrame,
            copiedFrame.nextElementSibling,
        );

        newFrameCanvasCtx.drawImage(
            copiedFrameCanvas,
            0,
            0,
            copiedFrameCanvas.width,
            copiedFrameCanvas.height,
        );

        this._changeCurrentFrame(newFrame);
        this._updateFrameNumbers();
    }

    _updateFrameNumbers() {
        const childrens = this._framesContainer.children;
        const childrensArray = Array.from(childrens);

        childrensArray.forEach((_, i) => {
            if (i === childrensArray.length) return;
            childrensArray[i].dataset.number = i.toString();
        });
    }
}
