export function getColor(state, key) {
    let color;
    if (key === 1) {
        color = state.color;
    } else if (key === 3) {
        color = state.secondaryColor;
    }

    return color;
}

export function getCoordinates(event, delta) {
    const coordinates = [];
    coordinates.push(Math.floor(event.layerX / delta));
    coordinates.push(Math.floor(event.layerY / delta));
    return coordinates;
}

export function getPixelColor(x, y, ctx) {
    const data = ctx.getImageData(x, y, 1, 1).data;
    const r = rgbToHex(data[0]);
    const g = rgbToHex(data[1]);
    const b = rgbToHex(data[2]);

    return `#${r + g + b}`;
}

export function getPixelColorRGBA(x, y, ctx) {
    const data = ctx.getImageData(x, y, 1, 1).data;

    return data;
}

export function rgbToHex(val) {
    if (val > 255 || val < 0) {
        throw new Error();
    }

    let hex = Number(val).toString(16);
    if (hex.length < 2) {
        hex = `0${hex}`;
    }
    return hex;
}

// x, y - coordinates on canvas devided by delta ([127:127] - max)
export function matchStartColor(x, y, size, delta, colors, cursorColor) {
    const r = rgbToHex(colors[(x * 4 + y * size * 4) * delta + 0]);
    const g = rgbToHex(colors[(x * 4 + y * size * 4) * delta + 1]);
    const b = rgbToHex(colors[(x * 4 + y * size * 4) * delta + 2]);
    const hex = `#${r + g + b}`;
    if (hex === cursorColor) {
        return true;
    }

    return false;
}

export function checkPixelCoordinate(x, y, firstColor, secondaryColor) {
    let color = firstColor;
    if ((x + y) % 2) {
        color = firstColor;
    } else {
        color = secondaryColor;
    }

    return color;
}

export function getNewCoordinates(clickX, newX, clickY, newY) {
    const coordinates = [];
    let changedX = clickX;
    let changedY = clickY;

    if (clickX < newX) {
        changedX++;
    } else if (clickX > newX) {
        changedX--;
    }
    if (clickY < newY) {
        changedY++;
    } else if (clickY > newY) {
        changedY--;
    }

    coordinates.push(changedX);
    coordinates.push(changedY);

    return coordinates;
}
