import { keyShortcuts } from './keyShortcuts';

export default function keyListener(event) {
    const keyPressedCode = event.keyCode;
    const tools = keyShortcuts;
    const keyMessage = document.getElementById('key-message');
    keyMessage.classList.remove('key-massage-show');

    if (keyPressedCode === tools.pen.code) {
        this._removeListeners();
        this._changeActiveTool(tools.pen.element);
        this.state.tool = 'pen';
        this._canvas.addEventListener('mousedown', this._penEraserLogic);
    }

    if (keyPressedCode === tools.eraser.code) {
        this._removeListeners();
        this._changeActiveTool(tools.eraser.element);
        this.state.tool = 'eraser';
        this._canvas.addEventListener('mousedown', this._penEraserLogic);
    }

    if (keyPressedCode === tools.line.code) {
        this._removeListeners();
        this._changeActiveTool(tools.line.element);
        this.state.tool = 'stroke-line';
        this._canvas.addEventListener('mousedown', this._strokeLineLogic);
    }

    if (keyPressedCode === tools.bucket.code) {
        this._removeListeners();
        this._changeActiveTool(tools.bucket.element);
        this.state.tool = 'bucket';
        this._canvas.addEventListener('mousedown', this._bucketLogic);
    }

    if (keyPressedCode === tools.paintAll.code) {
        this._removeListeners();
        this._changeActiveTool(tools.paintAll.element);
        this.state.tool = 'paintAll';
        this._canvas.addEventListener('mousedown', this._paintAllLogic);
    }

    if (keyPressedCode === tools.rectangle.code) {
        this._removeListeners();
        this._changeActiveTool(tools.rectangle.element);
        this.state.tool = 'rectangle';
        this._canvas.addEventListener('mousedown', this._rectangleLogic);
    }

    if (keyPressedCode === tools.circle.code) {
        this._removeListeners();
        this._changeActiveTool(tools.circle.element);
        this.state.tool = 'circle';
        this._canvas.addEventListener('mousedown', this._circleLogic);
    }

    if (keyPressedCode === tools.dithering.code) {
        this._removeListeners();
        this._changeActiveTool(tools.dithering.element);
        this.state.tool = 'dithering';
        this._canvas.addEventListener('mousedown', this._ditheringLogic);
    }

    if (keyPressedCode === tools.lighten.code) {
        this._removeListeners();
        this._changeActiveTool(tools.lighten.element);
        this.state.tool = 'lighten';
        this._canvas.addEventListener('mousedown', this._lightenLogic);
    }

    if (keyPressedCode === tools.rotate.code) {
        this._removeListeners();
        this._changeActiveTool(tools.rotate.element);
        this.state.tool = 'rotate';
        this._canvas.addEventListener('mousedown', this._rotateLogic);
    }

    if (keyPressedCode === tools.move.code) {
        this._removeListeners();
        this._changeActiveTool(tools.move.element);
        this.state.tool = 'move';
        this._canvas.addEventListener('mousedown', this._moveLogic);
    }

    if (keyPressedCode === tools.mirror.code) {
        this._removeListeners();
        this._changeActiveTool(tools.mirror.element);
        this.state.tool = 'mirror';
        this._canvas.addEventListener('mousedown', this._mirrorPenLogic);
    }

    if (keyPressedCode === tools.colorPicker.code) {
        this._removeListeners();
        this._changeActiveTool(tools.colorPicker.element);
        this.state.tool = 'colorPicker';
        this._canvas.addEventListener('mousedown', this._colorPickerLogic);
    }

    if (keyPressedCode === tools.flip.code) {
        this._removeListeners();
        this._changeActiveTool(tools.flip.element);
        this.state.tool = 'flip';
        this._canvas.addEventListener('mousedown', this._flipLogic);
    }

    if (keyPressedCode === tools.magic.code) {
        this._removeListeners();
        this._changeActiveTool(tools.magic.element);
        this.state.tool = 'magic';
        this._canvas.addEventListener('mousedown', this._magicLogic);
    }
}
