import { getCoordinates, getPixelColor } from '../ToolsUtils';

export default function colorPicker(event) {
    const delta = this.state.deltaSize;
    const ctx = this._ctx;
    const input = this._colorSelect;
    const inputSecond = this._secondaryColorSelect;

    const keyPress = event.which;

    const coordinates = getCoordinates(event, delta);
    const clickX = coordinates[0];
    const clickY = coordinates[1];

    const cursorColor = getPixelColor(clickX * delta, clickY * delta, ctx);

    if (keyPress === 1) {
        this.state.color = cursorColor;
        input.value = cursorColor;
    } else if (keyPress === 3) {
        this.state.secondaryColor = cursorColor;
        inputSecond.value = cursorColor;
    }
}
