export default function dragAndDropFrames() {
    let previousVisited = null;
    let dragElement = null;
    let newElement = null;

    this._framesContainer.addEventListener('dragstart', e => {
        dragElement = e.target;
        e.target.style.opacity = '0.4';
    });

    this._framesContainer.addEventListener('dragover', e => {
        e.preventDefault();
        return false;
    });

    this._framesContainer.addEventListener('dragenter', e => {
        if (e.target.classList.contains('frames-container')) {
            return;
        }

        if (e.target.classList.contains('frame-canvas')) {
            e.target.parentElement.parentElement.classList.add('frame-over');
            previousVisited = e.target.parentElement.parentElement;
        }
    });

    this._framesContainer.addEventListener('dragleave', e => {
        if (checkTarget(e.target)) {
            return;
        }

        if (
            e.target.classList.contains('frame-canvas') &&
            e.target !== previousVisited
        ) {
            e.target.parentElement.parentElement.classList.remove('frame-over');
        }
    });

    this._framesContainer.addEventListener('drop', e => {
        e.stopPropagation();
        newElement = e.target.parentElement.parentElement;

        if (
            dragElement !== newElement &&
            e.target.parentElement.parentElement.classList.contains('frame')
        ) {
            swap(dragElement, newElement);
            this._updateFrameNumbers();
        }
    });

    this._framesContainer.addEventListener('dragend', e => {
        const childrens = this._framesContainer.children;
        const childrensArray = Array.from(childrens);
        e.target.style.opacity = '1';

        childrensArray.forEach((_, i) => {
            childrensArray[i].classList.remove('frame-over');
        });
    });
}

function swap(el, newEl) {
    const parent = el.parentElement;
    const temp = document.createElement('div');

    parent.insertBefore(temp, newEl.nextElementSibling);
    parent.insertBefore(newEl, el.nextElementSibling);
    parent.insertBefore(el, temp.nextElementSibling);
    temp.remove();
}

function checkTarget(el) {
    if (
        el.classList.contains('delete-frame') ||
        el.classList.contains('copy-frame') ||
        el.classList.contains('frame-canvas-container')
    ) {
        return true;
    }

    return false;
}
