import '../screens/canvas/index.scss';
import Tools from '../components/Tools/Tools';
import Frames from '../components/Frames/Frames';
import Export from '../components/FileManagement/Export/Export';
import Import from '../components/FileManagement/Import/Import';
import Canvas from '../screens/canvas/Canvas';

export default class App {
    constructor() {
        this._createSpriteBtn = document.getElementById('create-sprite');
        this._landingPage = document.querySelector('.landing-page');
        this._spritePage = document.querySelector('.sprite-page');
    }

    start() {
        this._addLandingPageListeners();

        const tools = new Tools();
        this._canvas = new Canvas(tools);
        this._frames = new Frames();
        this._export = new Export();
        this._import = new Import();

        tools.init();
        this._canvas.init();
    }

    _addLandingPageListeners() {
        this._createSpriteBtn.addEventListener('click', () => {
            this._landingPage.style.display = 'none';
            this._spritePage.style.display = 'flex';
        });
    }
}
