import penEraserLogic from './toolsLitenersLogic/penEraserLogic';
import strokeLineLogic from './toolsLitenersLogic/strokeLineLogic';
import changeColorLogic from './toolsLitenersLogic/changeColorLogic';
import paintAllLogic from './toolsLitenersLogic/paintAllLogic';
import rectangleLogic from './toolsLitenersLogic/rectangleLogic';
import CircleLogic from './toolsLitenersLogic/circleLogic';
import BucketLogic from './toolsLitenersLogic/bucketLogic';
import DitheringLogic from './toolsLitenersLogic/ditheringLogic';
import LightenLogic from './toolsLitenersLogic/lightenLogic';
import MoveLogic from './toolsLitenersLogic/moveLogic';
import MirrorPenLogic from './toolsLitenersLogic/mirrorPenLogic';
import ColorPickerLogic from './toolsLitenersLogic/colorPickerLogic';
import FlipLogic from './toolsLitenersLogic/flipLogic';
import RotateLogic from './toolsLitenersLogic/rotateLogic';
import MagicLogic from './toolsLitenersLogic/magicLogic';
import KeyListener from './KeyShortcuts/keyListener';
import changeToolKey from './KeyShortcuts/changeToolKey';

export default class Tools {
    constructor() {
        this.state = {
            tool: 'pen',
            color: '#f00000',
            secondaryColor: '#4413ec',
            deltaSize: 24,
            canvasSize: 32,
        };
        this._canvas = document.getElementById('canvas');
        this._ctx = this._canvas.getContext('2d');
        this._temporaryCanvas = document.getElementById('temporary-canvas');
        this._temporaryCtx = this._temporaryCanvas.getContext('2d');

        this._currentTool = document.querySelector('.tool-selected');
        this._colorSelect = document.getElementById('main-color');
        this._secondaryColorSelect = document.getElementById('secondary-color');

        this._keyboardElement = document.querySelector('.keyboard');
        this._keyboardModal = document.querySelector('.keyboard-modal');
        this._keyboardModalContainer = document.querySelector(
            '.modal-container',
        );

        this._penEraserLogic = penEraserLogic.bind(this);
        this._strokeLineLogic = strokeLineLogic.bind(this);
        this._changeColorLogic = changeColorLogic.bind(this);
        this._paintAllLogic = paintAllLogic.bind(this);
        this._rectangleLogic = rectangleLogic.bind(this);
        this._circleLogic = CircleLogic.bind(this);
        this._bucketLogic = BucketLogic.bind(this);
        this._rotateLogic = RotateLogic.bind(this);
        this._ditheringLogic = DitheringLogic.bind(this);
        this._lightenLogic = LightenLogic.bind(this);
        this._moveLogic = MoveLogic.bind(this);
        this._mirrorPenLogic = MirrorPenLogic.bind(this);
        this._colorPickerLogic = ColorPickerLogic.bind(this);
        this._flipLogic = FlipLogic.bind(this);
        this._magicLogic = MagicLogic.bind(this);
        this._keyListener = KeyListener.bind(this);
        this._changeToolKey = changeToolKey.bind(this);
    }

    init() {
        this._addToolsListeners();
    }

    getState() {
        return this.state;
    }

    _addToolsListeners() {
        const toolsContainer = document.querySelector('.tools-container');
        const toolsContainerModal = document.querySelector(
            '.tools-container-modal',
        );

        this._canvas.addEventListener('mousedown', this._penEraserLogic);

        toolsContainer.addEventListener('mousedown', e => {
            this._removeListeners();
            this._changeActiveTool(e.target);

            if (e.target.classList.contains('pen')) {
                this.state.tool = 'pen';
                this._canvas.addEventListener(
                    'mousedown',
                    this._penEraserLogic,
                );
            }

            if (e.target.classList.contains('eraser')) {
                this.state.tool = 'eraser';
                this._canvas.addEventListener(
                    'mousedown',
                    this._penEraserLogic,
                );
            }

            if (e.target.classList.contains('stroke-line')) {
                this.state.tool = 'stroke-line';
                this._canvas.addEventListener(
                    'mousedown',
                    this._strokeLineLogic,
                );
            }

            if (e.target.classList.contains('rotate')) {
                this.state.tool = 'rotate';
                this._canvas.addEventListener('mousedown', this._rotateLogic);
            }

            if (e.target.classList.contains('paint-all')) {
                this.state.tool = 'paint-all';
                this._canvas.addEventListener('mousedown', this._paintAllLogic);
            }

            if (e.target.classList.contains('rectangle')) {
                this.state.tool = 'rectangle';
                this._canvas.addEventListener(
                    'mousedown',
                    this._rectangleLogic,
                );
            }

            if (e.target.classList.contains('circle')) {
                this.state.tool = 'circle';
                this._canvas.addEventListener('mousedown', this._circleLogic);
            }

            if (e.target.classList.contains('bucket')) {
                this.state.tool = 'bucket';
                this._canvas.addEventListener('mousedown', this._bucketLogic);
            }

            if (e.target.classList.contains('dithering')) {
                this.state.tool = 'dithering';
                this._canvas.addEventListener(
                    'mousedown',
                    this._ditheringLogic,
                );
            }

            if (e.target.classList.contains('lighten')) {
                this.state.tool = 'lighten';
                this._canvas.addEventListener('mousedown', this._lightenLogic);
            }

            if (e.target.classList.contains('move')) {
                this.state.tool = 'move';
                this._canvas.addEventListener('mousedown', this._moveLogic);
            }

            if (e.target.classList.contains('mirror-pen')) {
                this.state.tool = 'mirror-pen';
                this._canvas.addEventListener(
                    'mousedown',
                    this._mirrorPenLogic,
                );
            }

            if (e.target.classList.contains('color-picker')) {
                this.state.tool = 'color-picker';
                this._canvas.addEventListener(
                    'mousedown',
                    this._colorPickerLogic,
                );
            }

            if (e.target.classList.contains('flip')) {
                this.state.tool = 'flip';
                this._canvas.addEventListener('mousedown', this._flipLogic);
            }

            if (e.target.classList.contains('magic')) {
                this.state.tool = 'magic';
                this._canvas.addEventListener('mousedown', this._magicLogic);
            }
        });

        this._colorSelect.addEventListener('change', this._changeColorLogic);
        this._secondaryColorSelect.addEventListener(
            'change',
            this._changeColorLogic,
        );

        this._keyboardElement.addEventListener('click', () => {
            this._keyboardModal.classList.toggle('modal-show');
        });

        this._keyboardModal.addEventListener('click', e => {
            if (
                e.target === this._keyboardModal &&
                this._keyboardModal.classList.contains('modal-show')
            ) {
                this._keyboardModal.classList.toggle('modal-show');
            }
        });

        document.addEventListener('keydown', this._keyListener);
        toolsContainerModal.addEventListener('click', this._changeToolKey);
    }

    _removeListeners() {
        this._canvas.removeEventListener('mousedown', this._penEraserLogic);
        this._canvas.removeEventListener('mousedown', this._strokeLineLogic);
        this._canvas.removeEventListener('mousedown', this._paintAllLogic);
        this._canvas.removeEventListener('mousedown', this._rectangleLogic);
        this._canvas.removeEventListener('mousedown', this._circleLogic);
        this._canvas.removeEventListener('mousedown', this._bucketLogic);
        this._canvas.removeEventListener('mousedown', this._rotateLogic);
        this._canvas.removeEventListener('mousedown', this._ditheringLogic);
        this._canvas.removeEventListener('mousedown', this._lightenLogic);
        this._canvas.removeEventListener('mousedown', this._moveLogic);
        this._canvas.removeEventListener('mousedown', this._mirrorPenLogic);
        this._canvas.removeEventListener('mousedown', this._colorPickerLogic);
        this._canvas.removeEventListener('mousedown', this._flipLogic);
        this._canvas.removeEventListener('mousedown', this._magicLogic);
    }

    _changeActiveTool(element) {
        if (element.classList.contains('tools-container')) {
            return;
        }

        if (element.classList.contains('tool')) {
            this._currentTool.classList.toggle('tool-selected');
            element.classList.toggle('tool-selected');
            this._currentTool = element;
            return;
        }

        this._changeActiveTool(element.parentElement);
    }
}
