export default class Canvas {
    constructor(tools) {
        this._canvas = document.getElementById('canvas');
        this._temporaryCanvas = document.getElementById('temporary-canvas');
        this._frameCanvas = document.querySelector('.frame-canvas');
        this._previewCanvas = document.querySelector('.preview-canvas');
        this._tools = tools;

        this._sizeContainer = document.querySelector('.canvas-resize');
        this._currentSizeElement = document.querySelector('.size-active');
        this._canvasSize = 768;
    }

    init() {
        this._initCanvas(this._canvasSize);
        this._addCanvasListeners();
    }

    _addCanvasListeners() {
        this._canvas.oncontextmenu = () => false;

        this._canvas.addEventListener('mousemove', e => {
            const delta = this._tools.state.deltaSize;
            const coordinatesElement = document.getElementById(
                'display-coordinates',
            );
            const mouseX = Math.floor(e.layerX / delta);
            const mouseY = Math.floor(e.layerY / delta);
            coordinatesElement.textContent = `[${mouseX}:${mouseY}]`;
        });

        this._sizeContainer.addEventListener('click', e => {
            if (e.target.classList.contains('size-32')) {
                this._changeSize(24, e.target, 32);
            }

            if (e.target.classList.contains('size-64')) {
                this._changeSize(12, e.target, 64);
            }

            if (e.target.classList.contains('size-128')) {
                this._changeSize(6, e.target, 128);
            }
        });
    }

    _changeSize(newDelta, element, newSize) {
        const frames = document.querySelectorAll('.frame-canvas');
        const mainCanvas = this._canvas;
        const sizeText = document.getElementById('display-size');

        this._resizeCanvas(newDelta, mainCanvas);
        frames.forEach(currentCanvas => {
            this._resizeCanvas(newDelta, currentCanvas);
        });

        this._currentSizeElement.classList.toggle('size-active');
        element.classList.toggle('size-active');
        this._currentSizeElement = element;
        this._tools.state.deltaSize = newDelta;
        this._tools.state.canvasSize = newSize;

        sizeText.textContent = `[${newSize}x${newSize}]`;
    }

    _resizeCanvas(newDelta, canvas) {
        const delta = this._tools.state.deltaSize;
        const coeff = newDelta / delta;
        const tempCanvas = document.getElementById('temporary-canvas');
        const tempCtx = tempCanvas.getContext('2d');
        const ctx = canvas.getContext('2d');
        const newWidth = tempCanvas.width * coeff;
        const newHeight = tempCanvas.height * coeff;
        ctx.imageSmoothingEnabled = false;

        tempCtx.drawImage(canvas, 0, 0, tempCanvas.width, tempCanvas.height);

        ctx.clearRect(0, 0, this._canvas.width, this._canvas.height);
        ctx.drawImage(tempCanvas, 0, 0, newWidth, newHeight);
        tempCtx.clearRect(0, 0, this._canvas.width, this._canvas.height);
    }

    _initCanvas(size) {
        this._canvas.width = size;
        this._canvas.height = size;
        this._canvas.style.width = `${size}px`;
        this._canvas.style.height = `${size}px`;

        this._temporaryCanvas.width = size;
        this._temporaryCanvas.height = size;
        this._temporaryCanvas.style.width = `${size}px`;
        this._temporaryCanvas.style.height = `${size}px`;

        this._frameCanvas.width = size;
        this._frameCanvas.height = size;
        this._frameCanvas.style.width = `${size / 8}px`;
        this._frameCanvas.style.height = `${size / 8}px`;

        this._previewCanvas.width = size;
        this._previewCanvas.height = size;
    }
}
