import { getColor, getCoordinates } from '../ToolsUtils';

export default function circleLogic(event) {
    const delta = this.state.deltaSize;
    const canvas = this._canvas;
    const ctx = this._ctx;
    const tempCanvas = this._temporaryCanvas;
    const tempCtx = this._temporaryCtx;

    const color = getColor(this.state, event.which);
    const coordinates = getCoordinates(event, delta);
    const clickX = coordinates[0];
    const clickY = coordinates[1];

    tempCanvas.style.zIndex = 1;
    tempCtx.lineWidth = delta;
    tempCtx.strokeStyle = color;
    tempCtx.fillStyle = color;
    ctx.fillStyle = color;

    function draw(e) {
        const newCoordinates = getCoordinates(e, delta);
        const mouseX = newCoordinates[0];
        const mouseY = newCoordinates[1];
        const newX = Math.abs(mouseX - clickX);
        const newY = Math.abs(mouseY - clickY);
        const radius = Math.max(newX, newY);

        tempCtx.clearRect(0, 0, canvas.width, canvas.height);
        pixelCircleDrawing(clickX, clickY, radius, tempCtx, delta);
    }

    tempCanvas.addEventListener('mousemove', draw);
    document.body.addEventListener('mouseup', function remove() {
        ctx.imageSmoothingEnabled = false;
        ctx.drawImage(tempCanvas, 0, 0, canvas.width, canvas.height);
        tempCtx.clearRect(0, 0, canvas.width, canvas.height);
        tempCanvas.style.zIndex = -1;

        tempCanvas.removeEventListener('mousemove', draw);
        document.body.removeEventListener('mouseup', remove);
    });
}

function pixelCircleDrawing(clickX, clickY, radius, tempCtx, delta) {
    let newRadius = 0;
    let oldRadius = radius;
    let d = 2 - 2 * radius;

    while (oldRadius >= 0) {
        tempCtx.fillRect(
            (clickX + newRadius) * delta,
            (clickY + oldRadius) * delta,
            delta,
            delta,
        );
        tempCtx.fillRect(
            (clickX - newRadius) * delta,
            (clickY - oldRadius) * delta,
            delta,
            delta,
        );
        tempCtx.fillRect(
            (clickX + newRadius) * delta,
            (clickY - oldRadius) * delta,
            delta,
            delta,
        );
        tempCtx.fillRect(
            (clickX - newRadius) * delta,
            (clickY + oldRadius) * delta,
            delta,
            delta,
        );

        if (d < 0) {
            newRadius++;
            d += 2 * newRadius + 1;
        } else if (d > 0) {
            oldRadius--;
            d -= 2 * oldRadius + 1;
        } else if (d === 0) {
            newRadius++;
            d += newRadius - oldRadius;
            oldRadius--;
        }
    }
}
