import {
    getCoordinates,
    checkPixelCoordinate,
    getNewCoordinates,
} from '../ToolsUtils';

export default function ditheringLogic(event) {
    const delta = this.state.deltaSize;
    const canvas = this._canvas;
    const ctx = this._ctx;

    const coordinates = getCoordinates(event, delta);
    let clickX = coordinates[0];
    let clickY = coordinates[1];

    const draw = e => {
        const newCoordinates = getCoordinates(e, delta);
        const newX = newCoordinates[0];
        const newY = newCoordinates[1];

        ctx.fillRect(newX * delta, newY * delta, delta, delta);

        while (clickX !== newX || clickY !== newY) {
            ctx.fillRect(clickX * delta, clickY * delta, delta, delta);

            const changed = getNewCoordinates(clickX, newX, clickY, newY);
            clickX = changed[0];
            clickY = changed[1];

            ctx.fillStyle = checkPixelCoordinate(
                clickX,
                clickY,
                this.state.color,
                this.state.secondaryColor,
            );
        }
    };

    ctx.fillStyle = checkPixelCoordinate(
        clickX,
        clickY,
        this.state.color,
        this.state.secondaryColor,
    );
    ctx.fillRect(clickX * delta, clickY * delta, delta, delta);
    canvas.addEventListener('mousemove', draw);

    document.addEventListener('mouseup', function remove() {
        canvas.removeEventListener('mousemove', draw);
        document.removeEventListener('mouseup', remove);
    });
}
