export default class GoogleSignIn {
    constructor() {
        this._signInBtn = document.querySelector('.g-signin-btn');
        this._signOutBtn = document.querySelector('.g-sign-out-btn');
        this._signInData = document.querySelector('.signin-data');
        this._userName = document.querySelector('.g-user-name');
        this._profileImg = document.querySelector('.signin-pic');
    }

    loadGoogleApiLibrary() {
        window.gapi.load('client:auth2', {
            callback: () => {
                window.gapi.client
                    .init({
                        clientId:
                            '908616802053-tvj283ns99bpr3f728d4lfb1vbu3id23.apps.googleusercontent.com',
                        apiKey: 'AIzaSyDTtK3VdmQicgM2qUUCv7Q3RJ5DleFcZak',
                        scope:
                            'profile https://www.googleapis.com/auth/drive.file',
                    })
                    .then(() => {
                        this._signInBtn.addEventListener(
                            'click',
                            this._googleSignIn.bind(this),
                        );

                        this._signOutBtn.addEventListener(
                            'click',
                            this._googleSignOut.bind(this),
                        );
                    });
            },
        });
    }

    _googleSignIn() {
        window.gapi.auth2
            .getAuthInstance()
            .signIn()
            .then(success => {
                const name = success.w3.ig;
                const profile = success.getBasicProfile();
                const picUrl = profile.getImageUrl();

                this._profileImg.src = picUrl;
                this._userName.textContent = name;
                this._signInBtn.style.display = 'none';
                this._signInData.style.display = 'block';
            });
    }

    _googleSignOut() {
        window.gapi.auth2
            .getAuthInstance()
            .signOut()
            .then(() => {
                this._signInBtn.style.display = 'block';
                this._signInData.style.display = 'none';
            });
    }
}
