import GIFEncoder from 'gifencoder';
import Preview from '../../../screens/Preview/Preview';
import { download, createLayoutArray, createPNG } from '../fileManagementUtils';

export default class Export {
    constructor() {
        this._exportGifElement = document.getElementById('gif-export');
        this._exportApngElement = document.getElementById('apng-export');
        this._gDriveExportBtn = document.getElementById('gdrive-export');
        this._googleForm = document.getElementById('g-export-form');
        this._piskelExport = document.getElementById('piskel-export');
        this._googleDriveError = document.querySelector('.google-drive-error');
        this._gifSize = 128; // acceptable performance

        this._addExportButtonsListeners();
    }

    static getJSONFileUrl(file) {
        const blob = new Blob([JSON.stringify(file)], {
            type: 'application/json',
        });

        return URL.createObjectURL(blob);
    }

    _addExportButtonsListeners() {
        this._exportGifElement.addEventListener(
            'click',
            this._downloadAnimatedFile.bind(this, 'gif'),
        );

        this._exportApngElement.addEventListener(
            'click',
            this._downloadAnimatedFile.bind(this, 'apng'),
        );

        this._piskelExport.addEventListener(
            'click',
            this._downloadAsPiskel.bind(this),
        );

        this._googleForm.addEventListener(
            'change',
            this._gDriveExport.bind(this),
        );
    }

    _gDriveExport(event) {
        event.preventDefault();

        let accessToken;

        try {
            accessToken = window.gapi.auth.getToken().access_token;
        } catch (error) {
            this._googleForm.reset();
            this._googleDriveError.style.display = 'block';
            setTimeout(() => {
                this._googleDriveError.style.display = 'none';
            }, 5000);
            return;
        }

        const formData = new FormData();
        const metadata = {
            name: 'gif',
            mimeType: 'image/gif',
        };
        const file = new Blob([this._gDriveExportBtn.files[0]], {
            type: 'image/gif',
        });

        formData.append(
            'metadata',
            new Blob([JSON.stringify(metadata)], { type: 'application/json' }),
        );
        formData.append('file', file);

        fetch(
            'https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id',
            {
                method: 'POST',
                headers: new Headers({
                    Authorization: `Bearer ${accessToken}`,
                }),
                body: formData,
            },
        )
            .then(res => res.json())
            .then(val => {
                window.open(`https://drive.google.com/file/d/${val.id}/view`);
            });

        this._googleForm.reset();
    }

    _downloadAnimatedFile(type) {
        const frames = Array.from(document.querySelectorAll('.frame-canvas'));
        const size = this._gifSize;
        const fps = Preview.getFPS();
        const delay = Math.floor(1000 / fps);
        const encoder = new GIFEncoder(size, size);

        encoder.start();
        encoder.setRepeat(0);
        encoder.setDelay(delay);
        encoder.setQuality(20);

        frames.forEach(currentCanvas => {
            const tempCanvas = document.createElement('canvas');
            const tempCtx = tempCanvas.getContext('2d');
            tempCanvas.width = size;
            tempCanvas.height = size;

            tempCtx.drawImage(currentCanvas, 0, 0, size, size);

            encoder.addFrame(tempCtx);

            tempCtx.clearRect(0, 0, size, size);
        });

        encoder.finish();

        const binaryGif = encoder.out.data;
        const data = `data:image/${type};base64,${btoa(
            String.fromCharCode.apply(null, binaryGif),
        )}`;

        download(data, `download.${type}`);
    }

    _downloadAsPiskel() {
        const fileUrl = this._createPiskelFile();

        download(fileUrl, 'download.piskel');
    }

    _createPiskelFile() {
        const currentSizeElement = document.querySelector('.size-active');
        const currentFrames = Array.from(
            document.querySelectorAll('.frame-canvas'),
        );
        const size = currentSizeElement.dataset.number;
        const maxSize = this._gifSize;

        if (size > maxSize) {
            throw new Error();
        }

        const file = {};
        file.modelVersion = 2;
        file.piskel = {};
        file.piskel.name = 'download';
        file.piskel.fps = Preview.getFPS();
        file.piskel.width = size;
        file.piskel.height = size;
        file.piskel.description = '';
        file.piskel.hiddenFrames = [];
        file.piskel.layers = [];

        const layersInfo = {};
        layersInfo.name = 'Layer 1';
        layersInfo.opacity = 1;
        layersInfo.frameCount = currentFrames.length;
        layersInfo.chunks = [];

        const chunk = {};
        chunk.base64PNG = createPNG(currentFrames, size);
        chunk.layout = createLayoutArray(currentFrames.length);

        layersInfo.chunks.push(chunk);
        file.piskel.layers[0] = JSON.stringify(layersInfo);

        return Export.getJSONFileUrl(file);
    }
}
